import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { Navbar } from './components/Navbar';
import { About } from './pages/About';
import { Home } from './pages/Home';
import './App';
import { EnglishIt } from './pages/EnglishIt.js';
import StartTests from './pages/StartTests';

function App() {
  return (
    <BrowserRouter>

    <Navbar />
    <div className="container pt-4">
     
      <Switch>
        <Route path={'/'} exact component={Home} />
        <Route path={'/about'} component={About} />
        <Route path={'/englishIt'} component={EnglishIt} />
        <Route path={'/startTest'}  render={ () => <StartTests live={23}  /> }/>
        <Route path={'/startTest1'}  render={ () => <StartTests live={15}  /> }/>
        <Route path={'/startTest2'}  render={ () => <StartTests live={7}  /> }/>
        <Route path={'/startTest3'}  render={ () => <StartTests live={0} /> }/>
      </Switch>
    </div>
    </BrowserRouter>
  );
}

export default App;
