import React from 'react';
import { CSSTransition } from 'react-transition-group';


const VariantsTest = (props) => {
    return (
       <CSSTransition in={!props.variantsItem ? false: true} classNames='alert' timeout={300} unmountOnExit>
        <button onClick={() => props.onClick(props.variantsItem)}
    className="btn btn-primary btn-variants active" type="button">
    {props.variantsItem}</button></CSSTransition>)
}

export default VariantsTest;