const DELETE_ITEM = "DELETE_ITEM";



let initialState = {
    test: [{question: "cancel", variants: ["создать", "отменить","согласовать","закрыть","отдать"], correct:"отменить"},
    {question: "with", variants: ["на", "в","с","из","для"], correct:"с"},
    {question: "open", variants: ["cоздать", "отменить","согласовать","открыть","отдать"], correct:"открыть"},
    {question: "close", variants: ["создать", "отменить","согласовать","закрыть","отдать"], correct:"закрыть"},
    {question: "error", variants: ["поиск", "ошибка","успешно","взять","отдать"], correct:"ошибка"},
    {question: "store", variants: ["предмет", "ручка","хранилище","чистилище","клуб"], correct:"хранилище"},
    {question: "item", variants: ["включатель", "амулет","предмет","слово","буква"], correct:"предмет"},
    {question: "action", variants: ["актив", "предмет","внимание","действие","магазин"], correct:"действие"},
    {question: "type", variants: ["туннель", "вид","проход","секрет","тип"], correct:"тип"},
    {question: "page", variants: ["файл", "папка","хранилище","страница","предмет"], correct:"страница"},
    {question: "delete", variants: ["удалить", "отменить","согласовать","открыть","отдать"], correct:"удалить"},
    {question: "state", variants: ["актив", "предмет","внимание","действие","состояние"], correct:"состояние"},
    {question: "default", variants: ["падение", "подъем","по центру","по умолчанию","мероприятие"], correct:"по умолчанию"},
    {question: "return", variants: ["возвращать", "отменить","согласовать","открыть","отдать"], correct:"возвращать"}
],

};

const englishTestReducers = (state = initialState, action) => {
switch (action.type) {

    case DELETE_ITEM: 

    return {...state, test: [...state.test[action.indeX].variants[action.item] = null]

    };

    
            
    default:
        return state;
}

}

export const DeleteItem = (indeX, item) => (
    {type: DELETE_ITEM, indeX: indeX, item: item }
);



export default englishTestReducers;
