import React from 'react';
import { useState } from 'react';
import { CSSTransition } from 'react-transition-group';
import Info from '../components/info';
import { randomNumbersArray } from '../components/randomNumbersArray';
import { randomNumbersVariants } from '../components/randomNumbersVariants';

import { StartTestComponents } from '../components/StartTestComponents';

import TestData from '../data/testsData';



const StartTests = (props) => {

    const testData = TestData();

    const [randomNumber, setRandomNumber] = useState(randomNumbersArray(testData.length));
    const [questNumber, setQuestNumber] = useState(1);
    const [number, setNumber] = useState(1);
   
    const [finish, setFinish] = useState(testData.length - 1);
    const [live, setLive] = useState(props.live);
    const [levelLive, setLevelLive] = useState(3);
    const [useStateTest, setUseStateTest] = useState(testData[randomNumber[questNumber - 1]]);
    
    const [showTest, setShowTest] = useState(true);
    const [showInfo, setShowInfo] = useState(false);
    const [info, setInfo] = useState('');
    


     const handleClick = () => {
         setLive(live - 1)
         setLevelLive(levelLive - 1)
       
    } 

    const levelLiveClick = () => {
        setInfo('О Боже! Вы совершили кучу ошибок!')
        setRandomNumber(randomNumbersArray(testData.length))
        setNumber(1)
        setShowInfo(true)
    }

    const liveClick = () => {
        setInfo('У вас закончились права на ошибку')
        setRandomNumber(randomNumbersArray(testData.length))
        setNumber(1)
        setShowInfo(true)
     
    }

    const startTest = () => {
        
        setFinish(testData.length - 1)
        setLevelLive(3)
        setLive(props.live)
        setUseStateTest(testData[randomNumber[0]]);
        setQuestNumber(1)
        setShowInfo(false)
        
        
        
    }

    const nextClick = () => {
        setLevelLive(3)
        setQuestNumber(questNumber + 1)
        setFinish(finish - 1)
        setUseStateTest(testData[randomNumber[number]])
        setNumber(number + 1)
        setShowTest(false)
        showTestNew()
    }

    const showTestNew = () => {
        setShowTest(true)
    }


    const congratulation = () => {
        setInfo('Вы Победитель!')
        setNumber(1)
        setRandomNumber(randomNumbersArray(testData.length))
        setShowInfo(true)  
    }

   

    return(
        <div>
            {showTest && 
        <StartTestComponents state={useStateTest} live={live} finish={finish} 
        handleClick={handleClick} levelLiveClick={levelLiveClick} liveClick={liveClick} 
        congratulation={congratulation} nextClick={nextClick}  levelLive={levelLive} /> 
    } 

    <CSSTransition in={showInfo}
        timeout={300}
        classNames="alert"
        unmountOnExit
        onEnter={() => setShowTest(false)}
        onExited={() => setShowTest(true)}>
     <Info info={info} startTest={startTest} state={useStateTest} />  
     </CSSTransition> 
        <div className="alert alert-danger" role="alert">
            <ul className="list-group">
                <li className="list-group-item d-flex justify-content-between align-items-center">
                    Вопрос № {questNumber}
<span className="badge bg-primary rounded-pill"></span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center">
                   Вопросов осталось: {finish}
<span className="badge bg-primary rounded-pill"></span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center">
                    Право на ошибку: {live}
<span className="badge bg-primary rounded-pill"></span>
                </li>
            </ul>
        </div>
    </div>
    );
}

export default StartTests;