import React from 'react';
import { CSSTransition } from 'react-transition-group';


const Info = (props) => {
    return( <div  className="alert alert-dark" role="alert">
   <h1>{props.info}</h1>
   <h3>{props.state.question} - переводиться как: {props.state.correct}</h3>
 <button type="button" className="btn btn-outline-success" onClick={() => {props.startTest()}}>Начать сначала</button>
   </div>)
}

export default Info;