import React, { useEffect } from 'react';
import { useState } from 'react';
import { SwitchTransition, CSSTransition } from "react-transition-group";
import { randomNumbersVariants } from './randomNumbersVariants';
import TestComponent from './TestComponent';
import TestComponent2 from './TestComponent2';
import { variantsCreator } from './variantsCreator';




import VariantsTest from './VariantsTest';


export const StartTestComponents = (props) => {
    const [variants, setVariants] = useState(variantsCreator(randomNumbersVariants(),props.state.variants)); 
    const [state, setState] = useState(true);

    useEffect(() => {
        setVariants(variantsCreator(randomNumbersVariants(),props.state.variants))
    }, [props.state])

    const variantsChange = (indeX) => {
        variants.splice(indeX, 1, null)
        let newVariants = variants   
        setVariants(newVariants) 
   } 
    
 let onClick = (t) => {
    let answer = t
    if (answer !== props.state.correct && props.live > 0 && props.levelLive > 0) {
            
       const searchItem = (item, arrayVariants) => {
        let i = 0;
        while (item !== arrayVariants[i]) {
            i++;
        }
        return i;
        }
       
        let indeX = searchItem(answer, variants) 
        variantsChange(indeX)
        props.handleClick()  
        
    }

    else if (answer !== props.state.correct && props.levelLive < 1) {
        props.levelLiveClick()
    }

    else if (answer !== props.state.correct && props.live < 1) {
        props.liveClick()
    }

    else if (answer === props.state.correct && props.finish !== 0) {
        props.nextClick()
        setState(state => !state)
    }

    else if (answer === props.state.correct && props.finish === 0) {
        props.congratulation()
    }
       
   
    }


    let variantsElements = variants.map(d => <VariantsTest onClick={onClick} variantsItem={d} />)


    return (
    <>
<SwitchTransition mode="out-in">
          <CSSTransition
            key={state}
            addEndListener={(node, done) => {
              node.addEventListener("transitionend", done, false);
            }}
            classNames="fade"
          >
        {state ? <TestComponent question={props.state.question} variantsElements={variantsElements} /> : <TestComponent2 question={props.state.question} variantsElements={variantsElements} />}
             
          </CSSTransition>
        </SwitchTransition>
    </>
          
    

)
}

